.monaco-workbench
	.notebookOverlay
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar {
	opacity: 0;
	display: inline-flex;
	position: absolute;
	height: 26px;
	top: -14px;
	/* this lines up the bottom toolbar border with the current line when on line 01 */
	z-index: var(--z-index-notebook-cell-toolbar);
}

.monaco-workbench
	.notebookOverlay
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-toolbar-dropdown-active
	.cell-title-toolbar {
	z-index: var(--z-index-notebook-cell-toolbar-dropdown-active);
}

.monaco-workbench
	.notebookOverlay
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar
	.action-item.menu-entry {
	width: 24px;
	height: 24px;
	display: flex;
	align-items: center;
	margin: 1px 2px;
}

.monaco-workbench
	.notebookOverlay
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar
	.action-item
	.action-label {
	display: flex;
	align-items: center;
	margin: auto;
}

.monaco-workbench
	.notebookOverlay
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar
	.action-item
	.monaco-dropdown {
	width: 100%;
}

.monaco-workbench
	.notebookOverlay
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar
	.action-item
	.monaco-dropdown
	.dropdown-label {
	display: flex;
}

/* toolbar visible on hover */
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list:focus-within
	> .monaco-scrollable-element
	> .monaco-list-rows:not(:hover)
	> .monaco-list-row.focused
	.cell-has-toolbar-actions
	.cell-title-toolbar,
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row:hover
	.cell-has-toolbar-actions
	.cell-title-toolbar,
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.markdown-cell-hover.cell-has-toolbar-actions
	.cell-title-toolbar,
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-has-toolbar-actions.cell-output-hover
	.cell-title-toolbar,
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-has-toolbar-actions:hover
	.cell-title-toolbar,
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar:hover,
.monaco-workbench
	.notebookOverlay.cell-toolbar-hover
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-toolbar-dropdown-active
	.cell-title-toolbar {
	opacity: 1;
}

/* toolbar visible on click */
.monaco-workbench
	.notebookOverlay.cell-toolbar-click
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row
	.cell-title-toolbar {
	visibility: hidden;
}
.monaco-workbench
	.notebookOverlay.cell-toolbar-click
	> .cell-list-container
	> .monaco-list
	> .monaco-scrollable-element
	> .monaco-list-rows
	> .monaco-list-row.focused
	.cell-title-toolbar {
	opacity: 1;
	visibility: visible;
}
